@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:300,400');

body {
  margin: 0;
  padding: 0;
  font-family: 'Source Code Pro', monospace, sans-serif;
  font-weight: 300;
  overflow-x: hidden;
}

main {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

section, article {
  width: 80vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

nav {
    width: 100vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    border-bottom: 1px solid #80deea;
}

a {
    text-decoration: none;
    color: black;
}

nav a {
  margin: 2em;
}

a:hover {
  color: #80deea;
}

h1 {
    margin: 0.5em;
    font-size: 4rem;
    font-weight: 300;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-left: 0.1em;
    border-right: 0.1em solid #80deea;
    -webkit-animation: blink-caret 0.75s step-end infinite;
            animation: blink-caret 0.75s step-end infinite;
}

h2 {
  font-size: 2.5rem;
  margin: 0;
  font-weight: 300;
}

img {
  -o-object-fit: cover;
     object-fit: cover;
}

span {
  font-size: 2.5rem;
  margin: 1em;
}

@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #80deea;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #80deea;
  }
}

.material-icons {
  font-size: 4rem;
  margin-right: 0.5em;
  top: 20px;
  left: 20px;
  position: relative;
}

@media screen and (max-width: 1600px) {
  h1 {
    font-size: 3em;
  }
}

@media screen and (max-width: 1150px) {
  section {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-align: center;
        align-items: center;
    width: 100vw;
  }
  h1 {
    font-size: 2.5em;
  }
}

@media screen and (max-width: 900px) {
  span {
    font-size: 1.75rem;
  }
  .material-icons {
    font-size: 2.5rem;
    top: 15px;
    left: 15px;
  }
}

@media screen and (max-width: 600px) {
  span {
    font-size: 1.1rem;
  }
  .material-icons {
    font-size: 1em;
    margin-right: 0.25em;
    top: 4px;
    left: 4px;
  }
}


