.ProjectCard {
  border: 1px solid #D4D4D4;
  border-radius: 0.5em;
  width: 22em;
  height: 45em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  margin: 3em;
}

.ProjectCard img {
  width: 20em;
  height: 20em;
  border-bottom: 1px solid #D4D4D4;
  padding: 1em;
}

.ProjectCard .content {
    padding: 2em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

@media screen and (max-width: 1700) {
  .ProjectCard {
    -webkit-transform: scale(0.3);
        -ms-transform: scale(0.3);
            transform: scale(0.3)
  }
}


