.Home section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  text-align: center;
}

.Home img {
  padding: 1em;
  border: 1px solid #D4D4D4;
  margin: 3em;
}

@media screen and (max-width: 1600px) {
  .Home img {
    width: 20em;
    height: 20em;
  }
}

@media screen and (max-width: 1150px) {
  .Home img {
    width: 15em;
    height: 15em;
  }
}


